<template>
  <div class="mt-1 mb-5" v-if="$store.state.token !== '' || token!== '' && this.$router.history.current['path'] !=='/factura' && this.$router.history.current['path']!=='/saleLotCattle' && this.$router.history.current['path'] !=='/tablero'" >
    <v-app-bar app flex color="black" dark>
      <v-app-bar-nav-icon @click="drawer = true" ></v-app-bar-nav-icon>

      <v-toolbar-title>Subasta Ganadera</v-toolbar-title>
      <v-spacer></v-spacer>

      <v-btn @click="salir()" class="red outlined " >
        salir
        <v-icon>mdi-door-open</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer temporary v-model="drawer" absolute color="black">
      <v-list>
        <v-list-item>
          <h1></h1>
        </v-list-item>

        <v-list-item class="d-flex justify-center" to="/perfil">
          <v-avatar color="yellow" size="62">
            <span class="black--text font-weight-black text-h5">{{ iniciales }}</span>
          </v-avatar>
        </v-list-item>
        <v-list-item class="d-flex justify-center white--text mt-4 mb-4">
          <h3 align="center" justify="center">{{$store.state.datos.name}}</h3>
        </v-list-item>

        <!-- //Pagina inicio -->

        <v-list-item to="/home" >
          <v-list-item-icon class="white--text" >
            <v-icon color="white">mdi-home</v-icon>
          </v-list-item-icon>

          <v-list-item-title class="white--text">Home</v-list-item-title>
        </v-list-item>

        <!-- //grupo holders -->
        
        <v-list-item to="/holder">
          <v-list-item-icon class="white--text">
            <v-icon color="white">mdi-account</v-icon>
          </v-list-item-icon>

          <v-list-item-title class="white--text">Holders</v-list-item-title>
        </v-list-item>

        <!-- //grupo subasta -->

        <v-list-item to="/sale" >
          <v-list-item-icon class="white--text">
            <v-icon color="white">mdi-account</v-icon>
          </v-list-item-icon>

          <v-list-item-title class="white--text">Subasta</v-list-item-title>
        </v-list-item>


      </v-list>
    </v-navigation-drawer>
  </div>
</template>
<script>
export default {
  name: "PageHeader",
  data() {
    return {
      drawer: false,
      group: null,
      token:localStorage.getItem('token'),
      data:{}
    };
  },
  methods: {
    salir() {
      this.$router.replace("/")
      this.$store.commit("setToken", "")
      this.$store.commit("setDatos", {})
    }
  },
  computed: {
    iniciales(){
      
      let datos=JSON.parse(localStorage.getItem("datos"))
      return datos.name.charAt(0).toUpperCase()
    }
  }
};
</script>

