<template>
  <v-app class="mt-16">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-card class="elevation-0">
            <h2>Participantes de la subasta</h2>
          </v-card>
        </v-col>
        <v-col cols="6">
          <v-card class="elevation-0 d-flex justify-end">
            <v-btn v-if="$store.state.subasta.state!=='2'" class="elevation-5" @click="nuevoParticipante()" outlined
              color="green">Nuevo participante</v-btn>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" v-for="(p, i) in participantes" :key="i">
          <v-card max-width="200" class="elevation-1">
            <v-card-title class="d-flex justify-center">
              <v-avatar color="red">
                <span class="white--text">{{ p.consecutiveholder }}</span>
              </v-avatar>
            </v-card-title>
            <v-card-text>
              <v-row align="center" class="mx-0">
                <span>Participante de la subasta </span>
              </v-row>
            </v-card-text>
            <v-divider class="mx-4"></v-divider>
            <v-card-title>Subasta No. {{p.sale.salenumber}}
              <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
              <template>
                <span class="green--text" text v-if="p.state=='1'">Activo</span>
                <span class="red--text" text v-else>Subastado</span>
              </template>
            </v-card-text>
            <v-card-actions>
              <v-btn outlined color="green" @click="editar(p)" v-if="$store.state.subasta.state!=='2'">Editar</v-btn>
              <v-spacer></v-spacer>
              <template v-if="$store.state.subasta.state!=='2'">
                <v-icon class="mr-2" color="green" @click="editar(l)" v-if="p.state=='0'">mdi-account-check</v-icon>
                <v-icon class="mr-2" color="red" @click="editar(l)" v-else>mdi-account-off</v-icon>
              </template>

            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <!-- Cuadro de dialogo para insertar participante -->

    <v-dialog v-model="dialog" persistent max-width="500px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card class="">
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" v-if="bd === false">
                <v-text-field label="No. Subasta" v-model="subasta" disabled></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="No. Paleta" v-model="paleta"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-autocomplete label="Participante..." :items="holders" v-model="proveedor" item-value="value">
                </v-autocomplete>
              </v-col>

            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-btn outlined color="green" @click="nuevoUsuario()">Agregar usuario</v-btn>
          <v-spacer></v-spacer>
          <v-btn class="elevation-3" outlined color="red" @click="cerrarDialog()">
            Cerrar
          </v-btn>
          <v-btn class="elevation-3" outlined color="green" @click="accionParticipante()">
            {{ etiqueta }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Cuadro de dialogo para insertar holder -->

    <v-dialog v-model="dialog2" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialogoHolder()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Guardar usuario</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Documento"
                  type="number"
                  v-model="documento"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Telefono"
                  v-model="telefono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre Completo"
                  v-model="nombre"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6" v-if="bd != true">
                <v-text-field label="Correo" v-model="correo"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Direccion"
                  v-model="direccion"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Ciudad"
                  v-model="ciudad"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 1"
                  v-model="numCuenta1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 2"
                  v-model="numCuenta2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco3"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 3"
                  v-model="numCuenta3"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre completo de referencia"
                  v-model="referido"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Telefono de referencia"
                  v-model="telReferido"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="red" @click="cerrarDialogoHolder()">
            Cerrar
          </v-btn>
          <v-btn outlined color="green" @click="agregarHolder()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>
  
<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "LotCattle",
  data() {
    return {
      dialog: false,
      bd: false,
      titulo: "Guardar Lote",
      etiqueta: "Guardar",
      subasta: this.$store.state.subasta.salenumber,
      idSubasta: this.$store.state.subasta._id,
      proveedor: "",
      paleta: null,
      idSaleHolder: "",
      lotes: [],
      holders: [],
      participantes: [],
      subastas: [],
      nombre: "",
      documento: "",
      telefono: "",
      correo: "",
      direccion: "",
      ciudad: "",
      referido:"",
      telReferido:"",
      banco1: "",
      banco2: "",
      banco3: "",
      numCuenta1: "",
      numCuenta2: "",
      numCuenta3: "",
      dialog2: false,
    };
  },
  methods: {
    listarParticipantes() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get(`saleholder/subasta/${this.$store.state.subasta._id}`, header)
        .then((res) => {
          console.log(res.data.saleHolder);
          this.participantes = res.data.saleHolder;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    listarProveedores() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("holder", header)
        .then((response) => {
          console.log(response);
          response.data.holder.forEach((h) => {
            this.holders.push({
              text: " documento: " + h.document + " - " + h.name,
              value: h._id,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    agregarHolder() {
      this.overlay = true;
      axios
        .post("holder", {
          document: this.documento,
            name: this.nombre,
            email: this.correo,
            password: this.documento,
            phone: this.telefono,
            ciudad: this.ciudad,
            direccion:this.direccion,
            banco1:this.banco1,
            banco1numero:this.numCuenta1,
            banco2: this.banco2,
            banco2numero:this.numCuenta2,
            banco3:this.banco3,
            banco3numero:this.numCuenta3,
            referidopor:this.referido
        })
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.limpiarCajasHolder();
          this.listarProveedores()
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    nuevoUsuario() {
      this.dialog2 = true
      this.dialog = false
    },

    cerrarDialogoHolder() {
      this.dialog2 = false;
      this.limpiarCajasHolder();
    },

    limpiarCajasHolder() {
      this.telefono = ""
      this.nombre = "";
      this.documento = ""
      this.correo = "";
      this.banco1 = "";
      this.banco2 = "";
      this.banco3 = "";
      this.numCuenta1 = ""
      this.numCuenta3 = ""
      this.numCuenta2 = ""
      this.direccion = ""
      this.ciudad = ""
    },

    subastar(lote) {
      console.log(lote);
      this.$store.dispatch("setLote", lote);
    },

    editar(p) {
      console.log(p);
      this.dialog = true;
      this.titulo = "Editar participante"
      this.etiqueta = "Editar"
      this.bd = true;
      this.proveedor = p.holder._id;
      this.paleta = p.consecutiveholder
      this.idSaleHolder=p._id
    },

    nuevoParticipante() {
      this.dialog = true;
      this.titulo = "Guardar participante"
      this.etiqueta = "Guardar"
      this.bd = false;
    },

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas()
    },

    accionParticipante() {
      if (this.bd === true) {
        console.log(this.idSaleHolder);
        this.editarPartcipante();
      } else {
        console.log("estoy agregando..");
        this.agregarPartcipante();
      }
    },

    agregarPartcipante() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post(
          "saleholder",
          {
            sale: this.idSubasta,
            holder: this.proveedor,
            consecutiveholder: this.paleta
          },
          header
        )
        .then((res) => {
          console.log(res.data);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.limpiarCajas()
          this.listarParticipantes();
          this.dialog = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    editarPartcipante() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .put(
          `saleholder/${this.idSaleHolder}`,
          {
            holder: this.proveedor,
            consecutiveholder: this.paleta
          },
          header
        )
        .then((res) => {
          console.log(res.data);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualizacion exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.limpiarCajas()
          this.listarParticipantes();
          this.dialog = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    limpiarCajas() {
      this.proveedor = ""
    }
  },
  mounted() {
    this.listarParticipantes();
    this.listarProveedores();
  },
};
</script>
  