<template>
  <v-container class="mt-16" >
    <v-row>
      <v-col cols="1"></v-col>
      <v-col cols="10">
        <v-row>
          <v-col cols="5" class="pa-0 ma-0">
            <v-card class="elevation-6" max-height="370">
              <v-img src="../assets/images/ganado 2.jpeg" max-height="370">
              </v-img>
            </v-card>
          </v-col>
          <v-col cols="7" class="pa-0 ma-0">
            <v-card class="elevation-6" max-height="370">
              <v-card-title class="d-flex justify-center">
                <h3>Inicio de Sesion</h3>
              </v-card-title>
              <v-card-text>
                <v-row align="center" justify="center">
                  <v-col cols="12" sm="8">
                    <v-text-field
                      v-model="correo"
                      label="Email"
                      outlined
                      dense
                      color="black"
                      class="mt-12"
                    />
                    <v-text-field
                      v-model="pass"
                      label="Password"
                      outlined
                      dense
                      color="black"
                      type="password"
                    />
                    <v-row>
                      <v-col cols="12" sm="12" class="mb-6">
                        <span
                          class="Subtitle 1 blue--text"
                          @click="recuperarClave()"
                          >Olvido la contraseña?</span
                        >
                      </v-col>
                    </v-row>
                    <v-btn
                      color="green"
                      class="black--text mb-7"
                      dark
                      block
                      tile
                      @click="login()"
                      >Iniciar Sesion
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="1"></v-col>
    </v-row>

    <v-dialog v-model="dialog2" max-width="400px" persistent>
      <v-card>
        <v-card-title> Selecione su lugar de trabajo </v-card-title>
        <v-card-text>
          <v-select
            :items="lugares"
            label="Lugar de trabajo..."
            item-value="value"
            v-model="lugar"
          ></v-select>
        </v-card-text>
        <v-card-actions>
          <v-btn outlined color="orange" @click="ingresarHome()">
            Ingresar
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="red" outlined @click="cerrar()"> Cerrar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog3" max-width="400px">
      <v-card>
        <v-card-title class="black white--text">
          <span>Introduce tu correo electrónico </span>
          <span> para recuperar cuenta</span>
        </v-card-title>
        <v-card-text>
          <v-col cols="12">
            <v-text-field
              type="email"
              label="Correo"
              v-model="recuperar"
            ></v-text-field>
          </v-col>
        </v-card-text>
        <v-card-actions>
          <v-spacer> </v-spacer>
          <v-btn @click="cambioClave()" class="orange">Enviar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PageLogin",
  data() {
    return {
      lugares: [],
      lugar: "",
      l: "",
      dialog2: false,
      dialog3: false,
      overlay: false,
      recuperar: "",
      correo: "",
      pass: "",
      show1: false,
      errores: "",
    };
  },
  methods: {
    login() {
      this.overlay = true;
      axios
        .post("holder/login", {
          email: this.correo,
          password: this.pass,
        })
        .then((response) => {
          console.log(response.data);
          this.$store.dispatch("setToken", response.data.token);
          localStorage.setItem("token",response.data.token)
          this.$store.dispatch("setDatos", response.data.holder);
          localStorage.setItem("datos", JSON.stringify(response.data.holder))
          this.$router.push("/home")
        })
        .catch((error) => {
          console.log(error);
          this.overlay = false;
        });
    },

    recuperarClave() {
      this.dialog3 = true;
    },

    cambioClave() {
      this.dialog3 = true;
      axios
        .post(
          `holder/requestpasswordreset`,
          {
            email: this.recuperar,
          }
        )
        .then((res) => {
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Correo de recuperacion enviado",
            showConfirmButton: false,
            timer: 2000,
          });
          this.recuperar = "";
        })
        .catch((err) => {
          console.log(err);
          Swal.fire({
            title: "<strong>Error en los datos</strong>",
            icon: "error",
            html: `<h4>${err.response.data}</h4>`,
            showCloseButton: true,
          });
        });
    },


    cerrar() {
      this.$store.commit("setToken", "");
      this.$store.commit("setDatos", {});
      this.$store.commit("setSubasta", "");
      this.$store.commit("setLote", "");
      this.dialog2 = false;
    },

    registrar_usuario() {
      this.overlay = true;
      console.log(this.password);
      axios
        .post("https://cat-biblioteca.herokuapp.com/api/holder", {
          document: this.documento,
          name: this.nombre,
          email: this.email,
          password: this.password,
          phone: this.telefono,
          ficha: this.ficha,
          rol: this.perfil,
        })
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.limpiarcajas();
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    limpiarcajas() {
      (this.nombre = ""),
        (this.password = ""),
        (this.documento = ""),
        (this.telefono = ""),
        (this.email = ""),
        (this.perfil = ""),
        (this.ficha = "");
    },
  },
};
</script>
<style scoped>
.v-application .rounded-bl-xl {
  border-bottom-left-radius: 300px !important;
}

.v-application .rounded-br-xl {
  border-bottom-right-radius: 300px !important;
}
</style>