<template>
  <div class="mb-10 mt-16">
    <div>
      <v-container
        class="green cont d-flex justify-center"
        style="height: 200px"
      >
        <v-avatar color="yellow" size="150" class="ava">
          <span class="black--text font-weight-black text-h2">{{ iniciales }}</span>
        </v-avatar>
      </v-container>
      <v-container class="d-flex justify-center mb-10">
        <v-row>
          <v-col cols="12" sm="2"></v-col>
          <v-col cols="12" sm="8" class="d-flex justify-center">
            <v-card class="mt-5 tar black white--text" width="500px" rounded="xl">
          <v-card-title> </v-card-title>
          <v-card-title class="d-flex justify-center mt-4">
            {{ nombre }}
          </v-card-title>
          <v-list class="d-flex justify-center">
            <v-list-item-group>
              <v-list-item>
                <v-list-item-icon>
                  <v-icon> mdi-account </v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title
                    >Documento: {{ documento }}</v-list-item-title
                  >
                </v-list-item-content>
              </v-list-item>
              <v-list-item>
                <v-list-item-icon>
                  <v-icon> mdi-email </v-icon>
                </v-list-item-icon>
                <v-list-item-title>Correo: {{ correo }}</v-list-item-title>
                <v-list-item-content> </v-list-item-content>
              </v-list-item>
              <v-list-item>
                <v-list-item-icon>
                  <v-icon> mdi-phone </v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title
                    >Telefono: {{ telefono }}</v-list-item-title
                  >
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green" text @click="abrir()" class="ml-3">
              <v-icon>mdi-pencil</v-icon>
              Actualizar Perfil
            </v-btn>
          </v-card-actions>
        </v-card>
          </v-col>
          <v-col cols="12" sm="2"></v-col>
        </v-row>
        
      </v-container>

      <!-- Cuadro de dialogo -->

      <v-dialog v-model="dialog" height="500" width="400">
        <div class="white" align="center" justify="center">
          <v-toolbar
          dark
          color="green"
        >
          <v-btn
            icon
            dark
            @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Registre sus datos</v-toolbar-title>
          </v-toolbar>
          <v-container>
            <v-row>
              <v-col cols="12"  class="mt-4">
                <v-card-text>
                  <v-row align="center" justify="center">
                    <v-col cols="12" align="center" justify="center">
                      <v-text-field
                        label="Nombre Completo"
                        v-model="nombre"
                        outlined
                        dense
                        color="orange"
                        autocomplete="false"
                      />
                      <v-text-field
                        label="Documento"
                        v-model="documento"
                        outlined
                        dense
                        color="orange"
                        autocomplete="false"
                      />
                      <v-text-field
                        label="Telefono"
                        v-model="telefono"
                        type="Number"
                        outlined
                        dense
                        color="orange"
                        autocomplete="false"
                      />
                      
                    </v-col>
                  </v-row>
                </v-card-text>
                <v-card-actions>
                  <v-btn class="green black--text mb-6" @click="editarHolder()"
                >Actualizar Datos</v-btn
              >
                </v-card-actions>
              </v-col>
              
            </v-row>
          </v-container>
        </div>
      </v-dialog>
    </div>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </div>
</template>
<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PagePerfil",
  data() {
    return {
      rolesAdmin: ["ADMIN", "PUBLICO", "APRENDIZ", "INSTRUCTOR", "ADMINISTRATIVO","BIBLIOTECARIA","BIBLIOTECA","GUARDA","PORTERIA"],
      rolesBiblioteca: ["PUBLICO", "APRENDIZ", "INSTRUCTOR", "ADMINISTRATIVO","BIBLIOTECARIA","BIBLIOTECA"],
      rolesPorteria:["PUBLICO", "APRENDIZ", "INSTRUCTOR", "ADMINISTRATIVO","GUARDA","PORTERIA"],
      roles:["PUBLICO", "APRENDIZ", "INSTRUCTOR", "ADMINISTRATIVO"],
      dialog: false,
      dialog2: false,
      img: "",
      overlay: false,
      nombre: this.$store.state.datos.name,
      perfil: this.$store.state.datos.rol,
      documento: this.$store.state.datos.document,
      ficha: this.$store.state.datos.ficha,
      correo: this.$store.state.datos.email,
      telefono: this.$store.state.datos.phone,
      imagen: this.$store.state.datos.photo,
    };
  },
  methods: {
    login() {
      this.$router.replace("/");
    },
    abrir() {
      this.dialog = true;
    },
 
    subir(e) {
      this.img = e.target.files[0];
      console.log(this.img);
      let fd = new FormData();
      fd.append("archivo", this.img);
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(fd);
      axios
        .post(
          `https://cat-biblioteca.herokuapp.com/api/holder/uploadcloud/${this.$store.state.datos._id}`,
          fd,
          header
        )
        .then((response) => {
          console.log(response.data.url);
          this.imagen = response.data.url;
          this.$store.dispatch("setFoto", response.data.url);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    editarHolder() {
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.documento);
      axios
        .put(
          `holder/${this.id}`,
          {
            document: this.documento,
            name: this.nombre,
            phone: this.telefono,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualización exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarHolders();
          this.limpiarCajas();
          this.dialog = false;
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualización rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

  },

  computed: {
    iniciales(){
      return this.$store.state.datos.name.charAt(0).toUpperCase()
    }
  },

  mount() {
    this.llenar_perfil();
  },
};
</script>

<style>
.ava {
  position: relative;
  top: 120px;

  border: 5px solid;
  z-index: 2;
}

.cont {
  border-radius: 0px 0px 50px 50px;
}

.cont2 {
  border-radius: 50px 50px 0px 0px;
  border: 2px solid;
  border-color: black;
}

.cont3 {
  border-radius: 0px 0px 50px 50px;
  border: 2px solid;
  border-color: black;
}
</style>

