<template>
  <v-app class="mt-16">
    <template>
      <v-container fluid class="mt-5">
        <v-row>
          <v-col cols="6">
            <v-card class="elevation-0">
              <h2>Subastas</h2>
            </v-card>
          </v-col>
          <v-col cols="6">
            <v-card class="elevation-0 d-flex justify-end">
              <v-btn @click="nuevaSubasta()" outlined color="green"
                >Nueva Subasta</v-btn
              >
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <v-data-table
        :headers="encabezados"
        :items="subastas"
        :items-per-page="20"
        class="elevation-1 mt-5 mx-5"
      >
        <template v-slot:[`item.opciones`]="{ item }" >
          <div v-if="item.state!='2'">
          <template v-if="item.state === '1'">
            <v-icon class="mr-2" color="red" @click="habilitar(item)">
              mdi-account-off</v-icon
            >
          </template>

          <template v-else>
            <v-icon class="mr-2" color="green" @click="habilitar(item)"
              >mdi-account-check</v-icon
            >
          </template>
          <v-tooltip left >
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                dark
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="green"
                @click="editar(item)"
              >
                mdi-pencil</v-icon
              >
            </template>
            <span>Editar Subasta</span>
          </v-tooltip>
        </div>
        </template>

        <template v-slot:[`item.lotes`]="{ item }">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="black"
                @click="verLotes(item)"
              >
                mdi-cow
              </v-icon>
            </template>
            <span>Ver lotes</span>
          </v-tooltip>
        </template>

        <template v-slot:[`item.terminar`]="{ item }" >
          <v-tooltip left v-if="item.state == '1' || item.state == '3'">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="black"
                @click="terminarSubasta(item)"
              >
                mdi-exit-to-app
              </v-icon>
            </template>
            <span>Terminar Subasta</span>
          </v-tooltip>
        </template>

        <template v-slot:[`item.participantes`]="{ item }">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-img
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="black"
                @click="verParticipantes(item)"
                src="../assets/icons/licitacion.png"
                max-height="40"
                max-width="40"
              >
              </v-img>
            </template>
            <span>Ver participantes</span>
          </v-tooltip>
        </template>
        <template v-slot:[`item.iniciar`]="{ item }">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-img
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="black"
                @click="verParticipantes(item)"
                src="../assets/icons/subasta.png"
                max-height="40"
                max-width="40"
              >
              </v-img>
            </template>
            <span>Iniciar subasta</span>
          </v-tooltip>
        </template>
        <template v-slot:[`item.state`]="{ item }">
          <span class="green--text" v-if="item.state === '1'">Activo</span>
          <span class="orange--text" v-if="item.state === '3'">En subasta</span>
          <span class="red--text" v-if="item.state === '2'">Subastado</span>
          <span class="red--text" v-if="item.state === '0'">Inactivo</span>
        </template>
        <template v-slot:[`item.createdAt`]="{ item }">
          <span> {{ fechaSalida(item.createdAt) }}</span>
        </template>
      </v-data-table>
    </template>

    <!-- Cuadro de dialogo para insertar holder -->

    <v-dialog v-model="dialog" persistent max-width="300px">
      <v-card>
        <v-card-title>
          <span class="text-h5">Guardar Subasta</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  label="Tipo de subasta"
                  v-model="tipo"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red" outlined @click="cerrarDialog()"> Cerrar </v-btn>
          <v-btn color="green" outlined @click="registrar_sale()">
            {{ etiqueta }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";

//import autotable from "jspdf-autotable";
export default {
  name: "PageSubasta",
  data() {
    return {
      overlay: false,
      dialog: false,
      estado: "Activo",
      tipo: "",
      bd: false,
      etiqueta: "Guardar",
      encabezados: [
        {
          text: "Numero de subasta",
          align: "left",
          sortable: false,
          value: "salenumber",

        },
        { text: "Tipo de subasta", value: "type" },
        { text: "Estado", value: "state" },
        { text: "Creado", value: "createdAt", sortable: false },
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Lotes", value: "lotes", sortable: false, },
        { text: "Participantes", value: "participantes", sortable: false, },
        { text: "Terminar Subasta", value: "terminar", sortable: false, },
      ],
      subastas: [],

    };
  },
  methods: {
    listarSubastas() {
      console.log(localStorage.getItem("token"));
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get(`sale`, header)
        .then((response) => {
          console.log(response);
          this.subastas = response.data.sale;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    nuevaSubasta() {
      this.dialog = true;
      this.etiqueta = "Guardar"
      this.bd = false;
    },
    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas();

    },

    editarSale() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .put(
          "sale",
          {
            type: this.tipo,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.dialog = false;
          this.listarSubastas()
          this.limpiarCajas();
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    guardarSale() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post(
          "sale",
          {
            type: this.tipo,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.dialog = false;
          this.limpiarCajas();
          this.listarSubastas()
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    registrar_sale() {
      this.overlay = true;
      if (this.bd === true) {
        this.editarSale()
      } else {
        this.guardarSale()
      }

    },

    editar(item) {
      this.dialog = true;
      this.bd = true
      this.etiqueta = "Editar"
      console.log(item);
    },

    verLotes(item) {
      console.log(item._id);
      console.log(item);
      localStorage.setItem("numSubasta", item.salenumber)
      localStorage.setItem("subasta", item._id)
      //this.$store.dispatch("setSubasta", item)
      //this.$router.push("/lotCattle")
      //window.open("http://localhost:8080/#/lotCattle", "_blank");
      window.open("https://subasta-ganadera.herokuapp.com/#/lotCattle", "_blank");
    },

    verParticipantes(item) {
      console.log(item._id);
      console.log(item.salenumber);
      this.$store.dispatch("setSubasta", item)
      this.$router.push("/saleHolder")
    },

    terminarSubasta(item){
      console.log(item);
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea terminar la subasta?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          let header = { headers: { "x-token": this.$store.state.token } };
          axios.put(`sale/cerrarsubasta/${item._id}`,{},header)
          .then(res=>{
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Subasta Cerrada",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarSubastas()
          this.$store.state.subasta.state=res.data.sale.state
        })
          .catch(err=>{
          console.log(err);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${err.response.data.sale}</h4>`,
            showCloseButton: true,
          });

      })
    }
    })
  },
    habilitar(item) {
      console.log(item.state);
      if (item.state === "1") {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(`sale/unactivate/${item._id}`, {}, header)
          .then((response) => {
            console.log(response);
            Swal.fire({
              position: 'bottom-end',
              icon: 'success',
              title: 'Actualización exitosa',
              showConfirmButton: false,
              timer: 2000
            })
            this.listarSubastas();
          })
          .catch((error) => {
            console.log(error);
            Swal.fire({
              title: "<strong>Error en registro</strong>",
              icon: "error",
              html: `<h4>${error.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          });
      } else {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(`sale/activate/${item._id}`, {}, header)
          .then((response) => {
            console.log(response);
            this.listarSubastas();
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    limpiarCajas() {
      this.serial = "";
      this.observaciones = "";
    },

    fechaSalida(r) {
      let d = new Date(r);
      this.span = true
      return d.toLocaleDateString() + " - " + d.toLocaleTimeString();
    },

    
  },
  mounted() {
    this.listarSubastas();
  },
};
</script>