import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: "",
    datos: {},
    subasta: {},
    lote:{},
    estado:"",
    conectado:false
  },

  mutations: {
    setToken(state, valor) {
      state.token = valor
    },
    setDatos(state, valor) {
      state.datos = valor
    },
    setSubasta(state, value) {
      state.subasta = value
    },
    setLote(state, value) {
      state.lote = value
    },

    SOCKET_CONNECT(state){
      state.conectado=true
    },
    SOCKET_ACTUALIZARESTADO(state,valor){
      state.estado=valor
    }
  },
  actions: {
    setToken(context, data) {
      context.commit("setToken", data)
    },
    setDatos(context, data) {
      context.commit("setDatos", data)
    },
    setSubasta(context, data) {
      context.commit("setSubasta", data)
    },
    setLote(context, data) {
      context.commit("setLote", data)
    }
  },

})
