<template>
  <v-app class="mt-16">
    <template>
      <v-container fluid class="mt-5">
        <v-row>
          <v-col cols="6">
            <v-card class="elevation-0">
              <h2>Holders</h2>
            </v-card>
          </v-col>
          <v-col cols="6">
            <v-card class="elevation-0 d-flex justify-end">
              <v-btn class="elevation-5" outlined color="green" @click="nuevoHolder()">Nuevo Usuario</v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-container>

      <!-- tabla de datos de los holders -->

      <v-data-table
        :headers="encabezados"
        :items="holders"
        :items-per-page="20"
        class="elevation-1 mt-5 mx-5"
      >
        <template v-slot:[`item.opciones`]="{ item }">
          <template v-if="item.state === '1'">
            <v-icon class="mr-2" color="red" @click="editar(item)">
              mdi-account-off</v-icon
            >
          </template>
          <template v-else>
            <v-icon class="mr-2" color="green" @click="editar(item)"
              >mdi-account-check</v-icon
            >
          </template>
          <template>
            <v-icon class="mr-2" color="orange" @click="abrir(item)">
              mdi-pencil</v-icon
            >
          </template>
        </template>
        <template v-slot:[`item.state`]="{ item }">
          <span class="green--text" v-if="item.state === '1'">Activo</span>
          <span class="red--text" v-else>Inactivo</span>
        </template>
      </v-data-table>
    </template>

    <!-- Cuadro de dialogo para insertar holder -->

    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Documento"
                  type="number"
                  v-model="documento"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Telefono"
                  v-model="telefono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre Completo"
                  v-model="nombre"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6" v-if="bd != true">
                <v-text-field label="Correo" v-model="correo"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Direccion"
                  v-model="direccion"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Ciudad"
                  v-model="ciudad"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 1"
                  v-model="numCuenta1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 2"
                  v-model="numCuenta2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Banco"
                  v-model="banco3"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 3"
                  v-model="numCuenta3"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre completo de referencia"
                  v-model="referido"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Telefono de referencia"
                  v-model="telReferido"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="red"  @click="cerrarDialog()">
            Cerrar
          </v-btn>
          <v-btn outlined color="green" @click="accionHolder()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PagePersona",
  data() {
    return {
      dialog: false,
      overlay: false,
      titulo: "Guardar Usuario",
      id: "",
      bd: false,
      nombre: "",
      documento: "",
      telefono: "",
      direccion:"",
      correo: "",
      ciudad:"",
      referido:"",
      telReferido:"",
      banco1:"",
      banco2:"",
      banco3:"",
      numCuenta1:"",
      numCuenta2:"",
      numCuenta3:"",
      encabezados: [
        {
          text: "Documento",
          align: "start",
          sortable: false,
          value: "document",
        },
        { text: "Nombre", value: "name" },
        { text: "Telefono", value: "phone" },
        { text: "Correo", value: "email" },
        { text: "Estado", value: "state", sortable: false },
        { text: "Opciones", value: "opciones", sortable: false },
      ],
      holders: [],
    };
  },
  methods: {
    //funcion para traer todos ls holders

    listarHolders() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("holder", header)
        .then((response) => {
          console.log(response);
          this.holders = response.data.holder;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //metodo para el boton de nuevo holder abre el cuadro de dialogo

    nuevoHolder() {
      this.dialog = true;
      this.bd = false;
      this.titulo = "Guardar Holder";
    },

    //metodo que maneja el boton de guardar del dialogo
    // y decide si guarda o edita

    accionHolder() {
      if (this.bd === true) {
        console.log("estoy editando..");
        this.editarHolder();
      } else {
        console.log("estoy agregando..");
        this.agregarHolder();
      }
    },

    //metodo que limpia las cajas del formulario del dialogo

    limpiarCajas() {
      this.telefono=""
      this.nombre = "";
      this.documento = ""
      this.correo = "";
      this.banco1="";
      this.banco2="";
      this.banco3="";
      this.numCuenta1=""
      this.numCuenta2=""
      this.numCuenta3=""
      this.direccion=""
      this.ciudad=""
      this.referido=""
      this.telReferido=""
    },

    //metodo que esta asignado al boton editar de la tabla
    //abre el dialogo, edita el titulo y lleva los datos al formulario

    abrir(item) {
      console.log(item);
      this.titulo = "Editar Usuario";
      this.bd = true;
      this.dialog = true;
      this.id = item._id;
      console.log(this.id);
      (this.nombre = item.name),
        (this.documento = item.document),
        (this.telefono = item.phone),
        (this.correo = item.email),
        (this.perfil = item.rol),
        (this.ficha = item.ficha);
        this.banco1=item.banco1
        this.banco2=item.banco2
        this.banco3=item.banco3
        this.numCuenta1=item.banco1numero
        this.numCuenta2=item.banco2numero
        this.numCuenta3=item.banco3numero
        this.ciudad=item.ciudad
        this.direccion=item.direccion
        this.referido=item.referidopor
    },

    //metodo que edita holder en la base de datos

    editarHolder() {
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.documento);
      axios
        .put(
          `holder/${this.id}`,
          {
            document: this.documento,
            name: this.nombre,
            phone: this.telefono,
            ciudad: this.ciudad,
            direccion:this.direccion,
            banco1:this.banco1,
            banco1numero:this.numCuenta1,
            banco2: this.banco2,
            banco2numero:this.numCuenta2,
            banco3:this.banco3,
            banco3numero:this.numCuenta3,
            referidopor:this.referido
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualización exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarHolders();
          this.limpiarCajas();
          this.dialog = false;
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualización rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //metodo que guarda holder en la base de datos

    agregarHolder() {
      this.overlay = true;
      axios
        .post("holder", {
            document: this.documento,
            name: this.nombre,
            email: this.correo,
            password: this.documento,
            phone: this.telefono,
            ciudad: this.ciudad,
            direccion:this.direccion,
            banco1:this.banco1,
            banco1numero:this.numCuenta1,
            banco2: this.banco2,
            banco2numero:this.numCuenta2,
            banco3:this.banco3,
            banco3numero:this.numCuenta3,
            referidopor:this.referido
        })
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.listarHolders();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //cierra el dialogo y blanquea cajas

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas();
    },

    //metodo que activa y desactiva los holder esta asignado al boton
    // activa y desactivar de la tabla

    editar(item) {
      console.log(item.state);
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea cambiar el estado del usuario?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          
          if (item.state === "1") {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`holder/unactivate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarHolders();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          } else {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`holder/activate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarHolders();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          }
        }
      });
    },
  },
  mounted() {
    this.listarHolders();
  },
};
</script>