<template>
  <v-app class="mt-16">
    <template>
      <v-container fluid class="mt-5">
        <v-row>
          <v-col cols="6">
            <v-card class="elevation-0">
              <h2>Razas</h2>
            </v-card>
          </v-col>
          <v-col cols="6">
            <v-card class="elevation-0 d-flex justify-end">
              <v-btn class="elevation-5" outlined color="green" @click="nuevoHolder()">Nueva Raza</v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-container>

      <!-- tabla de datos de los razas -->

      <v-data-table :headers="encabezados" :items="breed" :items-per-page="20" class="elevation-1 mt-5 mx-5">
        <template v-slot:[`item.createdAt`]="{ item }">
          <span>{{ fecha(item.createdAt) }}</span>
        </template>
        <template v-slot:[`item.opciones`]="{ item }">
          <template v-if="item.state == '1'">
            <v-icon class="mr-2" color="red" @click="editar(item)">
              mdi-account-off</v-icon>
          </template>
          <template v-else>
            <v-icon class="mr-2" color="green" @click="editar(item)">mdi-account-check</v-icon>
          </template>
          <template>
            <v-icon class="mr-2" color="orange" @click="abrir(item)">
              mdi-pencil</v-icon>
          </template>
        </template>
        <template v-slot:[`item.state`]="{ item }">
          <span class="green--text" v-if="item.state === '1'">Activo</span>
          <span class="red--text" v-else>Inactivo</span>
        </template>
      </v-data-table>
    </template>

    <!-- Cuadro de dialogo para insertar raza -->

    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12"
              >
                <v-text-field label="Descripcion" type="text" v-model="descripcion"></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="red" @click="cerrarDialog()">
            Cerrar
          </v-btn>
          <v-btn outlined color="green" @click="accionRaza()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>
  
<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PageBreed",
  data() {
    return {
      dialog: false,
      overlay: false,
      titulo: "Guardar Raza",
      id: "",
      bd: false,
      descripcion: "",
      encabezados: [
        {
          text: "Descripcion",
          align: "start",
          sortable: false,
          value: "description",
        },
        { text: "Creada", value: "createdAt" },
        { text: "Estado", value: "state", sortable: false },
        { text: "Opciones", value: "opciones", sortable: false },
      ],
      breed: [],
    };
  },
  methods: {
    //funcion para traer todos ls holders

    listarBreed() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("breed", header)
        .then((response) => {
          console.log(response);
          this.breed = response.data.breed;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //metodo para el boton de nuevo holder abre el cuadro de dialogo

    nuevoHolder() {
      this.dialog = true;
      this.bd = false;
      this.titulo = "Guardar Holder";
    },

    //metodo que maneja el boton de guardar del dialogo
    // y decide si guarda o edita

    accionRaza() {
      if (this.bd === true) {
        console.log("estoy editando..");
        this.editarRaza();
      } else {
        console.log("estoy agregando..");
        this.agregarRaza();
      }
    },

    //metodo que limpia las cajas del formulario del dialogo

    limpiarCajas() {
      this.descripcion = ""
    },

    //metodo que esta asignado al boton editar de la tabla
    //abre el dialogo, edita el titulo y lleva los datos al formulario

    abrir(item) {
      console.log(item);
      this.titulo = "Editar Raza";
      this.bd = true;
      this.dialog = true;
      this.id = item._id;
      this.descripcion=item.description;
      console.log(this.descripcion);
    },

    //metodo que edita holder en la base de datos

    editarRaza() {
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.documento);
      axios
        .put(
          `breed/${this.id}`,
          {
            document: this.documento,
            name: this.nombre,
            phone: this.telefono,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualización exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarBreed();
          this.limpiarCajas();
          this.dialog = false;
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualización rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //metodo que guarda holder en la base de datos

    agregarRaza() {
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post("breed", {
          description: this.descripcion,
        },header)
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.listarBreed();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //cierra el dialogo y blanquea cajas

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas();
    },


    fecha(r) {
      let d = new Date(r);
      return d.toLocaleDateString() + " - " + d.toLocaleTimeString()
    },

    //metodo que activa y desactiva los holder esta asignado al boton
    // activa y desactivar de la tabla

    editar(item) {
      console.log(item.state);
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea cambiar el estado del usuario?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {

          if (item.state === "1") {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`breed/unactivate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarBreed();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          } else {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`breed/activate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarBreed();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          }
        }
      });
    },
  },
  mounted() {
    this.listarBreed();
  },
};
</script>