import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../pages/Login.vue'
import home from "../pages/Home.vue"
import holder from "../pages/Holder.vue"
import sale from "../pages/Sale.vue"
import lotCattle from "../pages/LotCattle.vue"
import saleHolder from "../pages/SaleHolder.vue"
import perfil from "../pages/Perfil.vue"
import resetear from "../pages/ResetearPassword.vue"
import saleLotCattle from "../pages/SaleLotCattle.vue"
import tablero from "../pages/Tablero.vue"
import factura from "../pages/Factura.vue"
import breed from "../pages/Breed.vue"


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: login
  },
  {
    path: '/resetear',
    name: 'resetear',
    component: resetear
  },
  {
    path: '/home',
    name: 'home',
    component: home
  },
  {
    path: '/perfil',
    name: 'perfil',
    component: perfil
  },
  {
    path: '/holder',
    name: 'holder',
    component: holder
  },
  {
    path: '/breed',
    name: 'breed',
    component: breed
  },
  {
    path: '/sale',
    name: 'sale',
    component: sale
  },
  {
    path: '/saleHolder',
    name: 'saleHolder',
    component: saleHolder
  },
  {
    path: '/lotCattle',
    name: 'lotCattle',
    component: lotCattle
  },
  {
    path: '/saleLotCattle',
    name: 'saleLotCattle',
    component: saleLotCattle
  },
  {
    path: '/tablero',
    name: 'tablero',
    component: tablero
  },
  {
    path: '/factura',
    name: 'factura',
    component: factura
  }
]

const router = new VueRouter({
  routes
})



export default router
